:root {
  --spacer-top: 75px;
  --color-main-neutral: #d4d4d4;
  font-size: 16px;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

* {
  font-kerning: none;
}

body {
  background-color: var(--color-main-neutral);
  width: 100%;
  height: 100%;
  text-transform: uppercase;
  letter-spacing: .5px;
  margin: 0;
  padding: 0;
  font-family: Fira Sans, sans-serif;
}

html::-webkit-scrollbar {
  display: none;
}

main {
  position: relative;
}

h1 {
  font-size: clamp(2rem, 7vw, 90px);
  line-height: 1;
}

h1 .word {
  overflow: hidden;
}

h2 {
  font-size: clamp(1.8rem, 5vw, 70px);
}

h3 {
  font-size: 1.2rem;
}

h4 {
  font-size: .9rem;
}

p {
  text-transform: none;
  line-height: 1.4;
}

.button {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.button__container {
  height: 24px;
  color: var(--color-main-neutral);
  text-align: center;
  background: #2c1741;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  padding: 4px 12px;
  font-size: .9rem;
  transition: all .2s ease-out;
  display: flex;
}

.button__top_text {
  opacity: 1;
  transition: all .2s ease-out;
  position: absolute;
}

.button__bottom_text {
  opacity: 0;
  transition: all .2s ease-out;
}

.button__container:hover {
  color: #000;
  background: #949494;
}

.button__container:hover .button__top_text {
  opacity: 0;
}

.button__container:hover .button__bottom_text {
  opacity: 1;
}

.button:active .button__bottom_text {
  color: #fff;
}

#tooltip {
  z-index: 200;
  visibility: visible;
  opacity: 0;
  color: #d4d4d4;
  background-color: #000;
  padding: .3rem .8rem;
  font-size: .6rem;
  transition: opacity .2s ease-out;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.slider__wrapper:hover #tooltip {
  visibility: visible;
  opacity: 1;
}

.loader {
  width: 100%;
  height: 100vh;
  z-index: 101;
  background-color: #949494;
  justify-content: center;
  align-items: center;
  transition: all .75s cubic-bezier(.645, .045, .355, 1) .5s;
  display: flex;
  position: fixed;
  inset: 0;
}

.loaded {
  transform: translateY(-100%);
}

nav {
  width: 100%;
  height: 60px;
  z-index: 100;
  background: var(--color-main-neutral);
  position: fixed;
  top: 0;
  left: 0;
}

.nav__background {
  height: 100%;
  align-items: center;
  padding: 0 20px;
  display: flex;
}

.nav__wrapper {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__wrapper.nav_logo {
  text-transform: none;
  width: auto;
  white-space: nowrap;
}

.nav__wrapper.nav_menu_wrapper {
  width: auto;
  display: flex;
  position: relative;
  overflow: hidden;
}

.nav__wrapper.nav_menu {
  gap: 30px;
  display: flex;
}

.nav__wrapper .nav_menu-item {
  white-space: nowrap;
  cursor: pointer;
}

.nav__wrapper.nav_menu_mobile {
  width: auto;
  white-space: nowrap;
  z-index: 101;
  cursor: pointer;
  position: absolute;
  right: 0;
  transform: translateY(100%);
}

.opener {
  margin: 200px 0 120px;
  padding: 0 20px;
}

.opener__title .line {
  overflow: hidden;
}

#studies {
