}

.project_image_container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 1;
  transition: all .3s cubic-bezier(.11, 0, .5, 0);
}

.projects__project:hover img {
  transform: scale(1);
}

.projects__project.project__textbox {
  height: auto;
  flex-direction: column;
  display: flex;
}

.project__textbox_line {
  width: 100%;
  height: 1px;
  background: #2b2b2b;
  margin: 7px 0;
}

.project__textbox_categories {
  justify-content: space-between;
  margin-bottom: 10px;
  display: flex;
}

.textbox__categories_categories {
  gap: 10px;
  display: flex;
}

#about {
  scroll-margin-top: var(--spacer-top);
}

.about {
  margin: 100px 0;
  padding: 0 20px;
}

.about .about__title {
  margin-bottom: 50px;
}

.about .about__text {
  gap: 20px;
