}

.about .about__text h3, .about .about__text_split {
  flex: 1 1 0;
  padding-right: 15%;
}

.about .about__text_split .text_split_text {
  margin-bottom: 30px;
}

#video {
  scroll-margin-top: var(--spacer-top);
}

.video__outer {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.video__inner {
  height: 100%;
  transform-origin: center;
  margin: 0 auto;
}

.video__inner_video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#designers {
  scroll-margin-top: var(--spacer-top);
}

.designers {
  width: 100%;
  margin: 100px 0 50px;
  padding: 0 20px;
}

.designers .designers__grid {
  grid-template-columns: repeat(4, 1fr);
  gap: 40px 20px;
  margin-top: 60px;
  display: grid;
}

.designers__grid_designer {
  padding-right: 15px;
