}

.designers__grid_client {
  flex-direction: column;
  display: flex;
}

.designers__grid_designer .designer_info {
  height: -moz-fit-content;
  height: fit-content;
  margin: 25px 0 60px;
}

.designers .designers__grid .button {
  position: absolute;
  bottom: 0;
  left: 0;
}

#clients {
  scroll-margin-top: var(--spacer-top);
}

.clients {
  margin: 100px 0;
  padding: 0 20px;
}

.clients h2 {
  margin-bottom: 40px;
}

.clients__wrapper.collapsible {
  cursor: pointer;
}

.clients__wrapper_grid {
  grid-column-gap: 20px;
  grid-template-columns: repeat(8, 1fr);
  grid-template-areas: "Area Area Area Area Area-2 Area-2 Area-2 Area-3";
  padding: 1rem 0;
  display: grid;
}

.grid__area_name {
  grid-area: Area;
}

.grid__area_categories {
  grid-area: Area-2;
}

.grid__area_more {
  grid-area: Area-3;
  justify-self: end;
}

.clients__content {
  max-height: 0;
  transition: max-height .2s ease-out;
  overflow: hidden;
}

.clients__content_text {
  color: #4a4a4a;
  margin-bottom: 1rem;
  padding-left: calc(50% + 10px);
}

.clients__line {
  width: 100%;
  height: 1px;
  background: #949494;
  transition: all .5s ease-out;
  position: relative;
}

.clients__line:after {
  content: "";
  z-index: 100;
  width: 100%;
  height: 100%;
  transform-origin: 100%;
  background: #2b2b2b;
  transition: transform .5s ease-out;
  position: absolute;
  left: 0;
  transform: scaleX(0);
}

.client:hover .clients__line:after {
  transform-origin: 0;
  transform: scaleX(1);
}

.line_active {
  background: #2b2b2b;
}

.slider {
  width: 100%;
  z-index: 10;
  background: linear-gradient(to top, #bdbdbd, var(--color-main-neutral));
  margin: 100px 0 0;
  padding: 20px 0;
  position: relative;
}

.slider__wrapper {
  height: 65vh;
  white-space: nowrap;
  cursor: grab;
  column-gap: 20px;
  display: flex;
  position: relative;
  overflow: hidden;
  overflow-x: hidden !important;
}

.image {
  width: auto;
  max-width: unset;
  height: 100%;
  object-fit: cover;
  background: #4682b4;
  margin-left: 20px;
  transition: all 1s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.image:nth-of-type(5) {
  margin-right: 20px;
}

.content {
  width: 100%;
  height: 100vh;
  z-index: 200;
  transition: all .5 ease-in-out;
  visibility: hidden;
  background-color: #0000;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.content .content__inner {
  width: 100%;
  height: calc(100vh - 60px);
  opacity: 1;
  transition: transform .5 ease-in-out;
  background: #d4d4d4;
  border-radius: 8px 8px 0 0;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px 20px 0;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.fixed {
  overflow: hidden;
}

#content_close {
  height: -moz-fit-content;
  height: fit-content;
  cursor: pointer;
  width: -moz-fit-content;
  width: fit-content;
}

.content__bottom {
  width: 100%;
  height: 50px;
  border-top: 1px solid #949494;
  justify-content: space-between;
  align-items: center;
  font-size: .8rem;
  display: flex;
  inset: auto 0 0;
}

.footer__outer {
  width: 100%;
  height: 100vh;
  z-index: 1;
  height: 100vh;
  padding: 0 20px;
  position: relative;
}

.footer__inner {
  width: 100%;
  height: 100%;
