  display: flex;
}

.footer__grid_inner {
  width: 100%;
  grid-template-rows: repeat(4, auto);
  grid-template-columns: repeat(8, 1fr);
  grid-template-areas: "Area Area Area Area Area Area Area Area"
                       "Area-1 Area-1 Area-1 Area-1 Area-2 Area-2 Area-3 Area-3"
                       "Area-4 Area-4 Area-4 Area-4 Area-5 Area-5 Area-5 Area-5"
                       "Area-6 Area-6 Area-6 Area-6 Area-6 Area-6 Area-6 Area-6";
  gap: 20px;
  display: grid;
}

.footer__bottom {
  width: 100%;
  height: 50px;
  border-top: 1px solid #949494;
  justify-content: space-between;
  align-items: center;
  font-size: .8rem;
  display: flex;
  position: absolute;
  inset: auto 0 0;
}

.grid__next {
  grid-area: Area;
  margin-bottom: 5rem;
}

.grid__contact {
  grid-area: Area-1;
}

.grid__contact .button__container {
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 25px;
}

.grid__inquiries {
  width: 100%;
  grid-area: Area-2;
}

.grid__location {
  grid-area: Area-3;
}

footer a {
  transition: all .25s;
}

footer a:hover {
  color: #4682b4;
}

@media screen and (max-width: 1000px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }

  .about .about__text {
    flex-direction: column;
  }

  .designers .designers__grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .video__outer {
    height: 75vh;
  }

  .clients__content_text {
    padding-left: 0;
  }

  .footer__bottom {
    text-align: center;
    flex-direction: column;
    justify-content: space-around;
    font-size: .7rem;
  }

  .grid__next {
    margin-bottom: 30px;
  }

  footer .button__container {
    display: none;
  }

  .grid__inquiries {
    grid-area: Area-2;
  }

  .grid__location {
    grid-area: Area-5;
  }
}

@media screen and (max-width: 650px) {
  .projects, .designers .designers__grid {
    grid-template-columns: 1fr;
  }

  .designers__grid_designer .designer_info {
    height: auto;
    margin: 25px 0 40px;
  }

  .clients__wrapper_grid {
    grid-column-gap: 20px;
    grid-template-columns: repeat(8, 1fr);
    grid-template-areas: "Area Area Area Area-2 Area-2 Area-2 Area-3 Area-3";
    padding: 1rem 0;
    display: grid;
  }

  .footer__grid_inner {
    grid-template-rows: repeat(4, auto);
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "Area Area Area Area"
                         "Area-1 Area-1 Area-1 Area-1"
                         "Area-2 Area-2 Area-2 Area-2"
                         "Area-3 Area-3 Area-3 Area-3";
  }

  .grid__location {
    grid-area: Area-3;
  }

  .nav__wrapper .nav_menu {
    display: none;
  }
}

a {
  display: inline-block;
  position: relative;
}

.nav_menu-item a:after {
  content: "";
  width: 100%;
  height: 1px;
  transform-origin: 100% 100%;
